
.App {
    font-family: Bahnschrift;
    text-align: center;
    position: relative;
    color: orange;
    font-size: 20px;
    width: 100vw;
    height: 100vh;
    background-color: black;
    z-index: 0;
}
.black-background {
    font-family: Bahnschrift;
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: black;
    z-index: -1;
}
.love {
  width: 100%;  
  height: 100%;
}
.love > img {
  height: 100%;
  width: auto;
}
.Artworks {
    font-family: Bahnschrift;
    color: orange;
    background: black;
}
.menu {
    font-family: Bahnschrift;
    color: orange;
    font-size: 10px;
    font: weight 10px;
    text-align: center;
    padding-top: 100px;
    font-size: 10px;
    font: weight 10px;
}
.row {
    font-family: Bahnschrift;
    color: orange;
}
body {
    font-family: Bahnschrift;
    text-align: center;
}
.black-nav {
    background: transparent;
    width: 100%;
    display: flex;
    color: orange;
    padding: 30px;
    font-family: Bahnschrift;
    font: weight 333px;
    font-size: 30px;
    text-align: center;
    box-sizing: border-box;
}
.list {
    font-family: Bahnschrift;
    color: orange;
    margin-top: 30px;
    text-align: left;
    padding-left: 20px;
    padding-right: 20px;
}

/*MENU*/
.About {
    text-align: left;
    font: lighter;
    font-family: Bahnschrift;
    color: orange;
    font-size: 10px;
    background-color: black;
    /* @media only screen and (min-width: 768px) {
    } */


    @media only screen and (max-width: 768px) {
      body{
      padding-left: 30px;
      padding-right: 30px;  
      }
    }
  
}
.AboutName {
    padding-top: 30px;
    text-align: center;
}

.modal{
  margin-top: 20px;
   padding: 20px;
   background: black;
}
.Artworks {
    font-family: Bahnschrift;
    background: black;
}
.Contact {
    font-family: Bahnschrift;
    padding-top: 280px;
    background: transparent;
    font-size: 30px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@font-face {
  font-family: yougi;
  src: url('./fonts/yougi.ttf')
}

@font-face {
  font-family: surround;
  src: url('./fonts/Cafe24SsurroundAir.ttf')
}

.App-header {
    font-family: Bahnschrift;
    background-color: #282c34;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: orange;
}
.CV {
    background-color: black;
    align-items: center;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}